export class getDevice {
  constructor(threshold = 1280, onChange = null, isConsole = false) {
    this.threshold = threshold; // 
    this.windowSize = this.getWindowSize()
    this.onChange = onChange
    this.isConsole = isConsole

    //  resize 
    this.onResize = this.onResize.bind(this)
    window.addEventListener('resize', this.onResize)
  }

  // 
  isMobile() {
    const userAgent = navigator.userAgent.toLowerCase();

    return {
      android: /android/i.test(userAgent),
      blackberry: /blackberry/i.test(userAgent),
      ios: /iphone|ipad|ipod/i.test(userAgent),
      opera: /opera mini/i.test(userAgent),
      windows: /iemobile/i.test(userAgent),
      any: /android|blackberry|iphone|ipad|ipod|opera mini|iemobile/i.test(userAgent),
    }
  }

  // 
  isTouchDevice() {
    return 'ontouchstart' in window || navigator.maxTouchPoints > 0 || navigator.msMaxTouchPoints > 0
  }

  // 
  getWindowSize() {
    return {
      width: window.innerWidth,
      height: window.innerHeight,
      isSmallScreen: window.innerWidth <= this.threshold, // 
    };
  }

  // 
  onResize() {
    this.windowSize = this.getWindowSize()

    if (typeof this.onChange === 'function') {
      this.onChange(this.windowSize);
    }

    //
    this.isConsole ? console.log(` : ${this.windowSize}`) : ''
  }

  //  resize 
  destroy() {
    window.removeEventListener('resize', this.onResize)
  }
}