<template>
  <div class="l-note" :class="{ 'sidebar-open': isSidebar }">
    <div class="l-note__hd">
      <div class="hd">
        <div class="hd__wp">
          <div class="logo u-imgfix:in" :style="logo ? { maskImage: `url(${logo})` } : {}"></div>
          <div class="menu">
            <div class="toggle" @click="sidebarToggle">
              <div class="toggle__bar"><span></span><span></span><span></span></div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="l-note__ctr">

      <div class="l-note__sidebar">
        <div class="sidebar">
          <div class="search">
            <div class="c-fm:noteSearch">
              <input type="text" placeholder="Search ..." id="sidebarSearch" class="c-fm__input"
                v-model.trim="searchTerm">
              <label for="#sidebarSearch" class="c-fm__icon" :class="{ 'is-ing': isSearchIcon }"
                @click="clearSearch"></label>
            </div>
          </div>

          <TransitionGroup tag="ul" :css="false" :onBeforeEnter="sidebar.onBeforeEnter" :onEnter="sidebar.onEnter"
            :onLeave="sidebar.onLeave" class="list">
            <li class="list__item" v-for="val in filteredItems" :key="val">
              <div class="list__link" @click="changeDetail(val)" :class="{'is-active' : val.name === activeName}">
                <div class="list__link-icon"></div>
                <div class="list__link-name">{{ val.name }}</div>
              </div>
            </li>
          </TransitionGroup>

          <div class="community">
            <div class="community__item" v-for="val in comunity" :key="val">
              <a :href="val.href" target="_blank" rel="noopener noreferrer" class="community__link"
                :class="'is-' + val.icon"></a>
            </div>
          </div>
        </div>
      </div>

      <div class="l-note__main">

        <div class="l-note__view">
          <Transition :css="false" :onBeforeEnter="view.onBeforeEnter" :onEnter="view.onEnter" :onLeave="view.onLeave">
            <div class="l-edit" :class="{ 'is-error' : isPageError }" v-html="markdownContent" :key="markdownContent" v-if="markdownContent !== undefined">
            </div>
            <router-view v-else-if="isDetailView && markdownContent === undefined" class="l-note-detail"></router-view>
            <div v-else class="e-tips"> Note Page ...</div>
          </Transition>
        </div>

        <div class="l-note__ft">
          <div class="copyright">
            <div class="copyright__text">Copyright JhuangPing 2019 - All. <span>Design by JhuangPing</span></div>
          </div>
        </div>

        <div class="btn-group">
          <div class="c-btn:gotop" @click="toTop">
            <div class="c-btn__icon"></div>
          </div>
        </div>
      </div>

    </div>

    <div class="el-mask" @click="sidebarToggle"></div>
  </div>
</template>

<script setup>
import { defineOptions, ref, defineProps, onMounted, toRaw, computed } from 'vue'
import { useRouter } from "vue-router"
import markdownIt from 'markdown-it'
// layout setting
import { getComunity } from '@api/note/getComunity'
// javscript
import { getDevice } from "@utils/getDevice"
import { useGsapTransitions } from './use/useGsapTransitions.js'

defineOptions({ name: 'LayoutNote' })

const props = defineProps({
  logo: String,
  type: String,
  buildSidebar: Object
})
const router = useRouter()
const device = new getDevice(1366)

// logo
const logo = computed(() => {
  return props.logo ? `${props.logo}` : ''
})

// GSAP 
const { sidebar, view } = useGsapTransitions()

// Comunity
const comunity = ref(getComunity().list)

const isSidebar = ref(true)

const sidebarToggle = () => {
  isSidebar.value = !isSidebar.value
}

// 
const newSidebar = ref([])
const activeName = ref('')
const setSidebar = () => {
  newSidebar.value = toRaw(props.buildSidebar)
  .filter(item => item.isShow === 1)
}

const activeSidebar = (name) => {
  activeName.value = name
}

// 
const searchTerm = ref('')
const filteredItems = computed(() => {
  // isShow : 1: true, 2: false, 3:  (jp:)
  // jp: 
  if (searchTerm.value.startsWith("jp:")) {
    const keyword = searchTerm.value.slice(3).toLowerCase()

    let searchList = props.buildSidebar.filter(item =>
      item.name.toLowerCase().includes(keyword)
    )
    return searchList.length ? searchList : props.buildSidebar.filter(item =>
      item.tags.toLowerCase().includes(keyword)
    )
  }

  // tag 
  if (searchTerm.value.startsWith("@")) {
    const keyword = searchTerm.value.slice(1).toLowerCase()
    return props.buildSidebar.filter(item =>
      item.isShow !== 3 && item.tags.toLowerCase().includes(keyword)
    )
  }

  // 
  return searchTerm.value !== '' ? 
    props.buildSidebar.filter(item =>
      item.isShow !== 3 && item.name.toLowerCase().includes(searchTerm.value.toLowerCase())
    ) 
    : newSidebar.value
})
const isSearchIcon = computed(() => {
  return searchTerm.value !== ''
})
const clearSearch = () => {
  searchTerm.value = ''
}

// 
const isPageError = ref(false)
const isDetailView = ref(false)
const markdownContent = ref()

// 
const clearDetail = () => {
  isDetailView.value = false
  markdownContent.value = undefined
}

const getDetail = async  (val) =>{
  try {
    //  fetch  Markdown 
    const response = await fetch(`./assets/${toRaw(props.type)}/file/${val.url}.${val.type}`)
    if (!response.ok) {
      throw new Error(`Failed to load Markdown file: ${response.statusText}`)
    }
    const rawMarkdown = await response.text(); //  Markdown 

    //  markdown-it  Markdown 
    const md = markdownIt()
    if (val.type === 'md') {
      markdownContent.value = md.render(rawMarkdown)
    } else {
      markdownContent.value = rawMarkdown
    }

    isPageError.value = false
  } catch (error) {
    isPageError.value = true
    console.error('Error loading Markdown file:', error)
    markdownContent.value = '<div class="error-tips"> Error loading content ?!</div>'
  }
}

// 
const changeDetail = async (val) => {
  activeSidebar(val.name)

  device.getWindowSize().isSmallScreen ? sidebarToggle() : ''

  switch (val.type) {
    case 'md':
      getDetail(val)

      break

    case 'html':
      getDetail(val)

      break

    case 'vue':
      clearDetail()
      isDetailView.value = true
      router.push({ name: val.url })

      break

    case 'link':
      router.push(val.url)

      break

    default:
      clearDetail()

      break
  }
}

const toTop = () => {
  document.querySelector('.l-note__view').scrollTo({
    top: 0,
    behavior: 'smooth',
  })
}

onMounted(() => {
  setSidebar();
})
</script>

<style lang="sass" scoped>
@import @sass/init
@import @sass/helpers

@import ./style
@import ./style/hackmd
@import ./style/edit
</style>
