export const setupScroll = () => {
  let viewHeight = 0;
  let viewElement;

  const calculateDimensions = () => {
    viewElement = document.querySelector('.l-note__view');

    if (!viewElement) {
      console.error(' .l-note__view ');
      return;
    }

    viewHeight = viewElement.scrollHeight || 0;
  };

  const onScroll = () => {
    if (!viewElement) return; //  viewElement 

    const scrollPosition = viewElement.scrollTop;
    const gotopElement = document.querySelector('.c-btn\\:gotop');

    if (!gotopElement) {
      console.error(' class ');
      return;
    }

    if (scrollPosition > 0) {
      gotopElement.classList.add('is-show');
      if (scrollPosition + viewElement.clientHeight >= viewHeight) {
        gotopElement.classList.remove('is-move');
      } else {
        gotopElement.classList.add('is-move');
      }
    } else {
      gotopElement.classList.remove('is-show');
    }
  };

  const initScroll = () => {
    calculateDimensions();
    window.addEventListener('resize', calculateDimensions);

    if (viewElement) {
      viewElement.addEventListener('scroll', () => {
        requestAnimationFrame(onScroll);
      });
    } else {
      console.error(' .l-note__view ');
    }
  };

  return { initScroll };
};
