import gsap from 'gsap'
import { setupScroll } from './useScroll'

export function useGsapTransitions() {
  const { initScroll } = setupScroll()

  const sidebar = {
    onBeforeEnter(el) {
      el.style.opacity = 0
      el.style.height = 0
    },
    onEnter(el, done) {
      gsap.to(el, {
        opacity: 1,
        height: '28.75px',
        delay: el.dataset.index * 0.15,
        onComplete: done
      })
    },
    onLeave(el, done) {
      gsap.to(el, {
        opacity: 0,
        height: 0,
        delay: el.dataset.index * 0.15,
        onComplete: done
      })
    }
  }

  const view = {
    onBeforeEnter(el) {
      el.style.opacity = 0
    },
    onEnter(el, done) {
      gsap.to(el, {
        opacity: 1,
        delay: 0.25,
        onComplete: () => {
          done()
          initScroll()
        }
      })
    },
    onLeave(el, done) {
      gsap.to(el, {
        opacity: 0,
        delay: 0.25,
        onComplete: done
      })
    }
  }

  return {
    sidebar,
    view
  }
}